@import url('https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
}

.round {
  display: none;
}

.socialcontainer {
  position: fixed;
  right: 0;
  top: 40%;
  display: flex;
  flex-direction: column;
  z-index: 30;
  font-size: 2em;
  color: #fff;
}



.active i {
  color: #000;
}

.socialcontainer i {
  margin: 10px;
}

.socialcontainer i:hover {
    transform: scale(1.1);
    transition: 0.2s;
}

section#sec-1 {
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #151b2b;
  display: flex;
  overflow: hidden;
}

section#sec-1 .left {
  width: 50%;
  height: 100%;
  justify-content: center;
  display: flex;
}

section#sec-1 .block {
  color: white;
  width: 450px;
  position: relative;
  top: 25%;
  z-index: 1;
}

section#sec-1 .block h1 {
  /*font-family: "Hkgrotesk";*/
  font-size: 56px;
  margin: 10px 0;
}

section#sec-1 .block h2 {
  /* font-family: "Sf ui text"; */
  font-size: 25px;
  margin: 10px 0;
  color: #8686b2;
}

section#sec-1 .block p {
  /* font-family: "Sf ui text"; */
  font-size: 16px;
  color: #8686b2;
}

section#sec-1 .right {
  background: transparent;
  width: 50%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  justify-content: center;
  align-items: center;
  display: flex;
}

section#sec-1 .right img{
  transform: translateY(-10%)
}

